<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <Progress>
      <template v-slot:progress-2>
        <div class="progress-border">
          <div class="progress">
            <span>2</span>
          </div>
        </div>
        <div class="progress-title"> & </div>
        <div class="progress-line"></div>
      </template>
    </Progress>
    <!--  -->
    <div class="row no-gutters mb-4">
      <div class="col-md-7">
        <div class="text-left bg-second text-main rounded p-1 pl-2"></div>
        <table class="table">
          <tbody>
            <tr class="p-2 border-bottom" v-for="item in cart" :key="item.id">
              <td>
                <img :src="item.product.imageUrl" alt width="120px" height="80" srcset />
              </td>
              <td>
                <a
                  href
                  class="text-main"
                  @click.prevent="openProduct(item.product.id)"
                >{{item.product.title}}</a>
              </td>
              <td>{{item.qty}}{{item.product.unit}}</td>
              <td class="text-right">{{item.total|currency}}</td>
            </tr>
          </tbody>
        </table>

        <div class="text-right p-2">{{getTotal|currency}}</div>
        <div class="text-right p-2" v-if="finalpay !== ''">{{finalpay|currency}}</div>
      </div>

      <!--  -->
      <div class="col-md-5">
        <form class="ml-md-2">
          <div class="row bg-third m-0 p-4">
            <div class="col-md-6 mb-2">
              <div class="form-group">
                <label for="name"></label>
                <input
                  name="name"
                  type="text"
                  class="form-control"
                  id="name"
                  v-model="checkInfor.name"
                  placeholder=""
                  v-validate="'required'"
                  :class="{'is-invalid':errors.has('name')}"
                />
                <small class="text-danger" v-if="errors.has('name')"></small>
              </div>
            </div>
            <div class="form-group col-md-6 mb-2">
              <label for="email">Email</label>
              <input
                id="email"
                type="email"
                class="form-control"
                name="email"
                v-model="checkInfor.email"
                placeholder="email"
                v-validate="'required|email'"
                :class="{'is-invalid':errors.has('email')}"
              />
              <small class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</small>
            </div>
            <div class="form-group col-12 mb-2">
              <label for="tel"></label>
              <input
                id="tel"
                type="tel"
                class="form-control"
                name="tel"
                v-model="checkInfor.tel"
                placeholder=""
                v-validate="'required'"
                :class="{'is-invalid':errors.has('tel')}"
              />
              <small class="text-danger" v-if="errors.has('tel')"></small>
            </div>
            <div class="form-group col-12 mb-2">
              <label for="address"></label>
              <input
                id="address"
                type="text"
                class="form-control"
                name="address"
                v-model="checkInfor.address"
                placeholder=""
                v-validate="'required'"
                :class="{'is-invalid':errors.has('address')}"
              />
              <small class="text-danger" v-if="errors.has('address')"></small>
            </div>
            <div class="form-group col-12 mb-2">
              <label for="textarea"></label>
              <input
                id="textarea"
                type="text"
                class="form-control"
                name="textarea"
                v-model="checkMessage"
                placeholder
                v-validate="'required'"
                :class="{'is-invalid':errors.has('textarea')}"
              />
              <small class="text-danger" v-if="errors.has('textarea')"></small>
            </div>
            <div class="form-group col-12 mb-2">
              <label for="coupon"></label>
              <div class="input-group mb-3">
                <input
                  id="coupon"
                  name="coupon"
                  type="text"
                  class="form-control"
                  placeholder
                  aria-label="Recipient's username"
                  aria-describedby="button-addon2"
                  v-model="recentCoupon"
                />
                <div class="input-group-append">
                  <button
                    class="btn btn-outline-general"
                    type="button"
                    id="button-addon2"
                    @click.prevent="checkCounpon"
                  ></button>
                </div>
              </div>
            </div>
            <span class="text-danger">
              
              <br />
            </span>
            <button type="button" class="btn btn-general ml-auto" @click.prevent="checkOrder"></button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery'
import Progress from '../components/Progress'

export default {
  data () {
    return {
      isLoading: false,
      cart: [],
      // getTotal: ',
      checkInfor: {
        name: '',
        email: '',
        tel: '',
        address: ''
      },
      recentCoupon: '',
      checkMessage: '',
      finalpay: '',
      getTotal: ''
    }
  },
  components: {
    Progress
  },
  methods: {
    getCart () {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      const vm = this
      vm.isLoading = true
      this.$http.get(api).then(response => {
        vm.cart = response.data.data.carts
        vm.getTotal = response.data.data.total
        console.log('', response)
        vm.isLoading = false
      })
      this.cart = this.$route.query.object
      vm.isLoading = false
    },
    checkOrder () {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`
      const vm = this
      const postInfor = {
        user: vm.checkInfor,
        message: vm.checkMessage
      }
      this.$validator.validate().then(valid => {
        if (valid) {
          // do stuff if not valid.
          this.$http.post(api, { data: postInfor }).then(response => {
            console.log('', response)
            vm.getCart()
            if (response.data.success) {
              this.$bus.$emit('message:push', response.data.message, 'main')
              this.$router.push({
                path: `/checkout-2/${response.data.orderId}`
              })
            } else {
              this.$bus.$emit('message:push', response.data.message, 'danger')
            }
          })
        } else {
          this.$bus.$emit('message:push', '', 'danger')
        }
      })
    },
    getOrders (page = 1) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/orders?page=${page}`
      this.$http.get(api).then(response => {
      })
    },
    openProduct (id) {
      let routerPush = this.$router.resolve({
        path: '/productpage',
        query: { id: id }
      })
      window.open(routerPush.href, '_blank')
      console.log(routerPush)
    },
    checkCounpon () {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`
      const vm = this
      vm.isLoading = true
      this.$http
        .post(api, { data: { code: vm.recentCoupon } })
        .then(response => {
          console.log('', response)
          vm.finalpay = response.data.data.final_total
          this.$bus.$emit('message:push', response.data.message, 'main')
          vm.isLoading = false
        })
    }
  },
  created () {
    this.getCart()
    this.getOrders()
  }
}
</script>

<style lang="scss" scoped>
.back {
  font-size: 16px;
  font-weight: bold;
  top: 20px;
  left: 10px;
}
.progress-border {
  margin-left: 10px;
  min-width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $first;
  .progress {
    position: relative;
    top: 5px;
    left: 5px;
    // border: white 2px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: $white;
    span {
      color: $first;
      font-weight: bold;
      font-size: 20px;
      position: absolute;
      top: 4%;
      left: 30%;
    }
  }
}
.progress-line {
  width: 5%;
  height: 4px;
  background-color: $general;
}
.progress-title {
  font-weight: bold;
  padding: 8px;
}
.unprogress-border {
  margin-left: 10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $shadow;
  .progress {
    position: relative;
    top: 5px;
    left: 5px;
    // border: white 2px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: $white;
    span {
      color: $shadow;
      font-weight: bold;
      font-size: 20px;
      position: absolute;
      top: 4%;
      left: 30%;
    }
  }
}
.unprogress-line {
  width: 75px;
  height: 4px;
  margin-top: 17.5px;
  background-color: $shadow;
}
td {
  border: none;
  text-align: left;
}
label {
  display: block;
  text-align: left;
  font-size: 14px;
}
</style>
